.profile {
  margin: 0 auto;
  padding: 0;
  display: grid;
  grid-template-columns: 120px 1fr 150px;
  column-gap: 30px;
}

@media screen and (max-width: 730px) {
  .profile {
    grid-template-columns: 1fr;
    justify-items: center;
    align-content: center;
    row-gap: 26px;
  }
}