.header {
  max-width: 880px;
  padding-bottom: calc(74px - 33px);
  margin: 45px auto 40px;
  border-bottom: 1px solid rgba(84, 84, 84, .7);
}


@media screen and (max-width: 730px) {
  .header {
    margin-top: 28px;
    margin-bottom: 42px;
    padding-bottom: 31.6px;
    max-width: 100%;
  }
}